import utils from "./utils";

const canvas = document.querySelector("canvas");
const c = canvas.getContext("2d");

canvas.width = window.innerWidth;
canvas.height = window.innerHeight;

const mouse = {
  x: null,
  y: null,
};

const gravity = 1;

const colors = ["#2185C5", "#7ECEFD", "#FFF6E5", "#FF7F66"];

// Event Listeners

addEventListener("resize", () => {
  canvas.width = window.innerWidth;
  canvas.height = window.innerHeight;

  init();
});

addEventListener("click", (event) => {
  mouse.x = event.clientX;
  mouse.y = event.clientY;
});

// Objects
class Ball {
  constructor(x, y, dx, dy, radius, color, bouncines) {
    this.x = x;
    this.y = y;
    this.dx = dx;
    this.dy = dy;
    this.radius = radius;
    this.color = color;
    this.bouncines = Math.min(bouncines, 1);
  }

  draw() {
    c.beginPath();
    c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
    c.fillStyle = this.color;
    c.fill();
    c.closePath();
  }

  update() {
    if (this.y + this.radius + this.dy > canvas.height) {
      this.dy = -this.dy * this.bouncines;
    } else {
      this.dy += gravity;
    }
    if (
      this.x + this.radius + this.dx > canvas.width ||
      this.x - this.radius + this.dx < 0
    ) {
      this.dx = -this.dx * this.bouncines;
    }
    if (this.dy == 0) {
      this.dy = 99999999999;
      this.dx = 0;
    }
    if (this.dy < 1) {
      if (mouse.x != null || mouse.y != null) {
        this.y = utils.randomIntFromRange(mouse.y - 30 * -1, mouse.y - 30);
        this.x = utils.randomIntFromRange(mouse.x - 30 * -1, mouse.x - 30);
      }
    }
    this.x += this.dx;
    this.y += this.dy;
    this.draw();
  }
}

// Implementation
let balls = [];
function init() {
  balls = [];
  for (let i = 0; i < 500; i++) {
    let radius = 10;
    let x = utils.randomIntFromRange(radius, canvas.width - radius);
    let y = canvas.height / 2;
    let dx = utils.randomIntFromRange(-4, 4);
    let dy = 2;
    let bounciness = utils.randomIntFromRange(500, 700) / 1000;
    let ball = new Ball(
      x,
      y,
      dx,
      dy,
      10,
      utils.randomColor(colors),
      bounciness
    );
    balls.push(ball);
  }
}

// Animation Loop
function animate() {
  requestAnimationFrame(animate);
  c.clearRect(0, 0, canvas.width, canvas.height);
  balls.map((ball) => ball.update());
  mouse.x = null;
  mouse.y = null;
}

init();
animate();
